<template>
  <transition name="operation">
    <div v-show="authority" id="accounting" class="func-operation">
      <div class="money-item pointer">
        <p>待审批</p>
      </div>
      <div class="money-item pointer">
        <p>全部</p>
      </div>
    </div>
  </transition>
</template>

<script>
export default {
  name: 'authority',
  data () {
    return {
      authority: false
    }
  },
  mounted () {
    this.authority = true
  }
}
</script>

<style scoped>
  .func-operation{
    height: auto;
    position: relative;
    z-index: 3;
  }
  .funcList-enter-active, .operation-enter-active{
    -webkit-transition: all .3s;
    -moz-transition: all .3s;
    -o-transition: all .3s;
    transition: all .3s;
  }
  .funcList-enter, .operation-enter{
    opacity: 0;
    -webkit-transform: translate3d(0, -100%, 0);
    -moz-transform: translate3d(0, -100%, 0);
    -ms-transform: translate3d(0, -100%, 0);
    transform: translate3d(0, -100%, 0);
  }
  .money-item{
    overflow: hidden;
    background-color: white;
    border-bottom: 1px solid #f0f2f5;
    position: relative;
    z-index: 3;
  }
  .money-item>p{
    font-size: 18px;
    overflow: hidden;
  }

  @media only screen and (max-device-width: 767px) {
    .func-operation {
      width: calc(100% - 40px);
      margin: 0 20px;
    }
    .money-item{
      width: 100%;
      height: 41px;
      padding: 8px 0;
    }
    .money-item>p{
      line-height: 41px;
    }
  }

  @media only screen and (min-device-width: 768px) {
    .func-operation {
      width: calc(100% - 36px);
      margin: 0 18px;
    }
    .money-item{
      width: calc(100% - 40px);
      height: 39px;
      padding: 10px 20px;
    }
    .money-item>p{
      line-height: 39px;
    }
  }

  @media only screen and (min-device-width: 768px) and (min-height: 960px) {
    #main{
      height: 600px;
    }
    #main p:first-of-type{
      margin-top: 240px;
    }
  }
</style>



